.App {
  text-align: center;
  height: calc(100vh - 80px);
  display: flex;
  font-family: 'Poppins', sans-serif;
}
.customers__title{
display: flex;
}
ul{
  list-style: none;
  margin: 0;
  padding: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.navShow{
  display: none;
  position: absolute;
right: 0;

}
.customers{
    padding: 41px 95px 0 71px;
    width: 100%;

}
@media screen and (max-width: 1100px) {
  /* .customers{
    padding: 41px 95px 0 10px;
 

} */
.nav{
position: fixed;
height: calc(100vh - 50px);

}
  .nav {
margin-left: -271px;
justify-content: center;
  }
  .retreat{
    margin-left: 0;
  }
  .navShow{
    display: block;
right: -50px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;

  width: 20px;
  height: 20px;
  border: solid black;
  border-width:  2px 2px 0 0;

  transform: rotate(45deg);
  transition: transform 0.3s ease;

  }
.retreat .navShow{display: none;}
.navShow:hover{
  cursor: pointer;
  border-color:blue;
}



}
@media screen and (max-width: 750px) {
.item__status {
  width: 25px;
}
  .customers{
    padding: 41px 10px 0 10px;
 

}
.item,.customers__list{
  font-size: 9px;
}
.search{
padding-bottom: 20px;
}
.search__btn{
/* padding: 10px 0; */
}
.clients{
padding: 5px;
}}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
